document.addEventListener('DOMContentLoaded', () => {
  const backdrop = document.querySelector('[data-order-backdrop]');
  const modal = document.querySelector('[data-order-modal]');
  const closeBtn = document.querySelector('[data-order-close]');
  const form = document.querySelector('#orderForm');

  // üîç –î–Ü–ê–ì–ù–û–°–¢–ò–ö–ê
  console.log({
    backdrop,
    modal,
    closeBtn,
    form,
  });

  if (!backdrop || !modal) {
    console.error(' Order modal not found in DOM');
    return;
  }

  function openOrderModal() {
    backdrop.classList.add('is-open');
    document.body.classList.add('modal-open');
  }

  function closeOrderModal() {
    backdrop.classList.remove('is-open');
    document.body.classList.remove('modal-open');
    form?.reset();
  }

  //  –ù–ï –≤—ñ–¥–∫—Ä–∏–≤–∞—î–º–æ –∞–≤—Ç–æ–º–∞—Ç–∏—á–Ω–æ
  // openOrderModal(); ‚Üê –í–ê–ñ–õ–ò–í–û: –¶–ï –í–ò–î–ê–õ–ò–¢–ò

  //  –í–Ü–î–ö–†–ò–¢–¢–Ø –î–õ–Ø –¢–ï–°–¢–£ –ó –ö–û–ù–°–û–õ–Ü
  window.openOrderModal = openOrderModal;

  // –ó–∞–∫—Ä–∏—Ç—Ç—è
  closeBtn?.addEventListener('click', closeOrderModal);

  backdrop.addEventListener('click', e => {
    if (e.target === backdrop) closeOrderModal();
  });

  window.addEventListener('keydown', e => {
    if (e.key === 'Escape') closeOrderModal();
  });

  // –°–∞–±–º—ñ—Ç
  form?.addEventListener('submit', e => {
    e.preventDefault();
    alert('submit test');
    closeOrderModal();
  });

  document.addEventListener('click', e => {
    if (e.target.closest('.modalAdoptBtn')) {
      openOrderModal();
    }
  });
});
